import gql from "graphql-tag"

const CHECK_USERNAME = gql`query checkUsername($username: String!) {
  user: usernameExists(username: $username)
}`

const LOGIN = gql`query login($username: String!, $password: String!) {
  login(username: $username, password: $password)
}`

const REGISTER = gql`query register($username: String!, $password: String!) {
  register(username: $username, password: $password) {
    id
    username
    timestamp
  }
}`

const ME = gql`query me {
  me {
    id
    username
    timestamp
    currentRoom {
      id
      name
      owner {username}
      lastMessages
      {
        id
        timestamp
        author
        {username}
        text
      }
    }
  }
}`

const ROOMS = gql`query rooms {
  rooms {
    id
    name
    owner {username}
  }
}`

const USERS = gql`query users {
  users {
    id
    username
    currentRoom {
      id
      name
    }
    rooms {id}
  }
}`

const CREATE_ROOM = gql`mutation createRoom($name: String!) {
  createRoom(name: $name) {
    id
    name
    owner {username}
  }
}`

const UPDATE_ROOM = gql`mutation updateRoom($id: ID!, $name: String!) {
  updateRoom(id: $id, name: $name) {
    id 
    name
    owner {username}
  }
}`

const DELETE_ROOM = gql`mutation deleteRoom($id: ID!) {
  deleteRoom(id: $id) {
    id 
    name
    owner {username}
  }
}`

const JOIN = gql`mutation join($roomId: ID!) {
  joinRoom(roomId: $roomId) {
    id
    name
    lastMessages
    {
      id
      timestamp
      author {username}
      text
    }
  }
}`

const LEAVE = gql`mutation leave {
  leaveCurrentRoom {
    id 
    name
  }
}`

const CREATE_MESSAGE = gql`mutation createMessage($text: String!) {
  createMessage(text: $text) {
    id
    timestamp
    author {username}
    text
  }
}`

const ROOM_CREATED = gql`subscription roomCreated {
  roomCreated {
    id name owner {username}
  }
}`

const ROOM_UPDATED = gql `subscription roomUpdated {
  roomUpdated {
    id name owner {username}
  }
}`

const ROOM_DELETED = gql`subscription roomDeleted {
  roomDeleted {
    id name owner {username}
  }
}`

const CURRENT_ROOM_CHANGED = gql`subscription currentRoomChanged {
  currentRoomChanged {
    currentRoom {id name}
  }
}`

const MEMBER_JOINED = gql`subscription memberJoined {
  memberJoined {
    id
    username
    currentRoom {
      id
      name
    }
    rooms {id}
  }
}`

const MEMBER_LEFT = gql`subscription memberLeft {
  memberLeft {id username}
}`

const MESSAGE_CREATED = gql`subscription messageCreated {
  messageCreated {
    id
    timestamp
    author {username}
    text
  }
}`

export const graphqlCommands = {
  CHECK_USERNAME,
  LOGIN,
  REGISTER,
  ME,
  ROOMS,
  USERS,
  CREATE_ROOM,
  UPDATE_ROOM,
  DELETE_ROOM,
  JOIN,
  LEAVE,
  CREATE_MESSAGE,
  ROOM_CREATED,
  ROOM_UPDATED,
  ROOM_DELETED,
  CURRENT_ROOM_CHANGED,
  MEMBER_JOINED,
  MEMBER_LEFT,
  MESSAGE_CREATED,
}