<template>
    <div id="app">
        <!--Page view when user isn't in chat room-->
      <div id="out_chat" v-show="!is_chatting">

          <!--Available chat rooms-->
        <div id="chat_list" class="item">
          <div v-bind:style="[rooms.length ? {'display': 'none'} : {'display': 'inline'}]">Loading...</div>
          <ul id="rendered_chat_list">
            <li v-for="(room, index) in rooms" :key="room.id" @click="toggleActive(index)"
              :class="{'active': index==active_index}" v-bind:style="[room.owner.username===me.username ?
              {'background-color': 'aqua'} : {'background-color': 'transparent'}]"><i><b>{{room.name}}</b></i></li>
          </ul>
        </div>

          <!--Various buttons-->
        <div id="out_chat_buttons" class="item">
            <!--Button to join selected chat-->
          <form id="join_form" v-on:submit.prevent="Join">
            <div>Selected chat room: {{active_room.name}}</div>
            <br>
            <button id="join_room" :disabled="!active_room.name.length">Join</button>
            <br><br>
          </form>

            <!--Message about chat rooms with blue background-->
          <div id="info"><b>Note, {{me.username}}: rooms that belong to you<br> have light blue background.
          <br><br>
          You will be able to change their name and<br> delete them after you join them.</b></div>
          <br><br>

            <!--Form to create new chat room-->
          <form id="create_form" v-on:submit.prevent="createRoom">
            <div>Enter new room's name<br>(you will be the owner of this room)</div>
            <br>
            <input v-model="new_chat_name" id="new_chat_name">
            <br><br>
            <button id="create_room" :disabled="!new_chat_name.length">Create new room</button>
            <br><br><br>
          </form>
        </div>
      </div>

        <!--Page view when user is in chat room-->
      <div id="in_chat" v-show="is_chatting">
          <!--Current chat room's users-->
        <div id="user_list" class="item">
          <div v-bind:style="[chat_users.length ? {'display': 'none'} : {'display': 'inline'}]">Loading...</div>
          <ul id="rendered_user_list">
            <li v-for="user in chat_users" :key="user.id" v-bind:style="[user.username===me.username ?
              {'background-color': 'aqua'} : {'background-color': 'transparent'}]"><i><b>{{user.username}}</b></i></li>
          </ul>
        </div>

          <!--Current chat room's messages-->
        <div id="messages_view" class="item">
          <ul id="rendered_message_list">
            <div v-bind:style="[messages.length ? {'display': 'none'} : {'display': 'inline'}]">No messages yet</div>
            <li v-for="message in messages" :key="message.id" v-bind:style="[message.author.username===me.username ?
              {'color': 'purple', 'text-align': 'right'} : {'color': 'black', 'text-align': 'left'}]">
              <p v-if="message.id.length!==0"><br>Sent by: {{message.author.username}}<br>{{message.text}}<br></p>
              <p v-else v-bind:style="{'text-align': 'center'}"><b><br>{{message.text}}<br></b></p></li>
          </ul>
        </div>

          <!--Various buttons-->
        <div id="in_chat_buttons" class="item">
          <div>Selected chat room: {{active_room.name}}</div><br>

            <!--Button to leave selected chat-->
          <form id="leave_form" v-on:submit.prevent="Leave">
            <button id="leave_room" :disabled="!chat_users.length">Leave</button>
            <br><br>
          </form>

            <!--Form to send messages in chat-->
          <form id="send_message_form" v-on:submit.prevent="createMessage">
            <p>Say something:</p>
            <input v-model="text" id="chat_message_text">
            <button id="send_message" :disabled="!text.length">Speak</button>
          </form><br>

            <!--Admin buttons (if chat belongs to user)-->
          <div id="admin_in_chat" v-show="me.username===active_room.owner.username">
              <!--Form to change current chat room's name-->
            <form id="rename_form" v-on:submit.prevent="updateRoom">
              <div>Enter current room's new name</div>
              <br>
              <input v-model="updated_chat_name" id="updated_chat_name">
              <br><br>
              <button id="rename_room" :disabled="!updated_chat_name.length">Rename current room</button>
              <br><br>
            </form>

              <!--Form to delete current chat room-->
            <form id="delete_form" v-on:submit.prevent="deleteRoom">
              <button id="delete_room" :disabled="!chat_users.length">Delete current room</button>
            </form>
          </div>

        </div>
      </div>

    </div>
</template>

<script>
import { graphqlCommands } from '../graphql/graphql.js'

export default {
  name: 'Chatrooms',
  data()
  {
    return {
      active_index: null,
      active_room: {
        id: '',
        name: '',
        owner: { 
          username: '',
        }
      },
      chat_users: [],
      is_chatting: false,
      me: {
        id: '',
        username: '',
        timestamp: '',
        currentRoom: {
          id: '',
          name: '',
          owner: {
            username: '',
          },
        }
      },
      messages: [],
      new_chat_name: '',
      rooms: [],
      text: '',
      updated_chat_name: '',
      users: [],
    }
  },
  apollo:
  {
    me: graphqlCommands.ME,
    rooms: graphqlCommands.ROOMS,
    users: graphqlCommands.USERS,
    $subscribe: {
      currentRoomChanged: {
        query: graphqlCommands.CURRENT_ROOM_CHANGED,
      },
      memberJoined: {
        query: graphqlCommands.MEMBER_JOINED,
        result({data}){
          if(this.me.id!==data.memberJoined.id && this.chat_users.findIndex(user => user.id===data.memberJoined.id)<0)
          {
            this.chat_users.push(data.memberJoined);
            this.messages.push({
              id: '',
              timestamp: Date.now(),
              author: {username: ''},
              text: `${data.memberJoined.username} joined`
            })
          }
        }
      },
      memberLeft: {
        query: graphqlCommands.MEMBER_LEFT,
        result({data}) {
          if(this.me.id!==data.memberLeft.id)
          {
            let index = this.chat_users.find((user) => user.id=data.memberLeft.id);
            this.chat_users.splice(index, 1);
            this.messages.push({
              id: '',
              timestamp: Date.now(),
              author: {username: ''},
              text: `${data.memberLeft.username} left`
            })
          }
        }
      },
      messageCreated: {
        query: graphqlCommands.MESSAGE_CREATED,
        result({data}) {
          this.messages.push({
            id: data.messageCreated.id,
            timestamp: data.messageCreated.timestamp,
            author: {username: data.messageCreated.author.username},
            text: data.messageCreated.text
          })
        }
      },
      roomCreated: {
        query: graphqlCommands.ROOM_CREATED,
        result({data}) {
          this.rooms.push({
            id: data.roomCreated.id,
            name: data.roomCreated.name,
            owner: {
              username: data.roomCreated.owner.username,
            },
          });
        }
      },
      roomUpdated: {
        query: graphqlCommands.ROOM_UPDATED,
        result({data}) {
          if(this.is_chatting)
          {
            this.active_room.name=data.roomUpdated.name;
            this.me.currentRoom.name=data.roomUpdated.name;
            this.messages.push({
              id: '',
              timestamp: Date.now(),
              author: {username: ''},
              text: `${this.me.username} renamed chat to ${data.roomUpdated.name}`
            });
          }
          else
          {
            let index = this.rooms.findIndex((room) => room.id===data.roomUpdated.id);
            this.rooms[index].name=data.roomUpdated.name;
            if(index===this.active_index)
              this.active_room.name=data.roomUpdated.name;
          }
        }
      },
      roomDeleted: {
        query: graphqlCommands.ROOM_DELETED,
        result({data}) {
          let index = this.rooms.findIndex((room) => room.id===data.roomDeleted.id);
          this.rooms.splice(index, 1);
          if(index===this.active_index)
          {
            this.active_index=null;
            this.active_room={id: '', name: '', owner: {username: ''} };
          }
          if(this.is_chatting)
          {
            alert('Admin decided that\'s enough discussion for today and deleted this room. '+
            '\nEveryone can go home now. Thank you for participating');
            this.updated_chat_name='';
            this.me.currentRoom={id: '', name: '', owner: {username: ''} };
            this.messages=[];
            this.chat_users=[];
            this.is_chatting=false;
          }
        }
      }
    },
  },
  methods:
  {
    Join()
    {
      this.is_chatting=true;
      this.new_chat_name='';
      this.$apollo.mutate({
        mutation: graphqlCommands.JOIN,
        variables: {
          roomId: this.active_room.id,
        }
      })
        .then(({data}) => {
          this.me.currentRoom=this.active_room;
          for(let i=0; i<data.joinRoom.lastMessages.length; i++)
          {
            this.messages.push({
                id: data.joinRoom.lastMessages[i].id,
                timestamp: data.joinRoom.lastMessages[i].timestamp,
                author: {username: data.joinRoom.lastMessages[i].author.username},
                text: data.joinRoom.lastMessages[i].text
              })
          }
          this.$apollo.queries.users.refetch()
            .then(({data}) => {
              this.chat_users = data.users.filter(user => user.currentRoom!==null &&
                user.currentRoom.id===this.me.currentRoom.id);
              if(this.chat_users.length===0 || this.chat_users.find(user => user.username===this.me.username)===undefined)
                this.chat_users.push(this.me);
            })
        })
    },
    Leave()
    {
      this.is_chatting=false;
      this.active_index=null;
      this.updated_chat_name='';
      this.$apollo.mutate({
        mutation: graphqlCommands.LEAVE,
      })
        .then(() => {
          this.me.currentRoom={id: '', name: '', owner: {username: ''} };
          this.active_room={id: '', name: '', owner: {username: ''} };
          this.messages=[];
          this.chat_users=[];
          this.$apollo.queries.rooms.refetch()
            .then(({data}) => {
              this.rooms=data.rooms;
            });
        })
    },
    createMessage()
    {
      this.$apollo.mutate({
        mutation: graphqlCommands.CREATE_MESSAGE,
        variables: {
          text: this.text,
        }
      })
        .then(() => {
          this.text=''
        })
    },
    createRoom()
    {
      this.$apollo.mutate({
        mutation: graphqlCommands.CREATE_ROOM,
        variables: {
          name: this.new_chat_name,
        }
      })
        .then(() => {
          this.new_chat_name=''
        })
    },
    updateRoom()
    {
      this.$apollo.mutate({
        mutation: graphqlCommands.UPDATE_ROOM,
        variables: {
          id: this.active_room.id,
          name: this.updated_chat_name,
        }
      })
        .then(() => {
          this.updated_chat_name=''
        })
    },
    deleteRoom()
    {
      this.$apollo.mutate({
        mutation: graphqlCommands.DELETE_ROOM,
        variables: {
          id: this.active_room.id,
        }
      })
    },
    toggleActive(index)
    {
      this.active_index=index;
      this.active_room=this.rooms[index];
    }
  },
  async created()
  {
    this.$apollo.queries.me.refetch()
    .then(({data}) => {
      this.me=data.me;
      const curr_room = data.me.currentRoom;
      if(curr_room===null || curr_room.id.length===0)
      {
        this.is_chatting=false;
        this.me.currentRoom = {
          id: '',
          name: '',
          owner: {
            username: '',
          },
        }
        this.$apollo.queries.rooms.refetch()
        .then(({data}) => {
          this.rooms=data.rooms;
        })
      }
      else
      {
        this.is_chatting=true;
        this.active_room = {
          id: curr_room.id,
          name: curr_room.name,
          owner: { 
            username: curr_room.owner.username,
          }
        }
        for(let i=0; i<curr_room.lastMessages.length; i++)
        {
          this.messages.push({
              id: curr_room.lastMessages[i].id,
              timestamp: curr_room.lastMessages[i].timestamp,
              author: {username: curr_room.lastMessages[i].author.username},
              text: curr_room.lastMessages[i].text
            })
        }
        this.$apollo.queries.users.refetch()
          .then(({data}) => {
            this.chat_users = data.users.filter(user => user.currentRoom!==null &&
              user.currentRoom.id===this.me.currentRoom.id);
            if(this.chat_users.length===0 || this.chat_users.find(user => user.username===this.me.username)===undefined)
              this.chat_users.push(this.me);
          })
      }
    });
  }
}
</script>

<style scoped>
.active
{
  color: red;
}
#chat_list
{
  overflow: scroll;
}
#info
{
  background-color: aqua;
}
.item
{
  display: inline-block;
  height: 500px;
  overflow: scroll;
  vertical-align: middle;
  width: 400px;
}
li
{
  text-align: center;
  height: 40px;
}
ul
{
  list-style-type: none;
  height: 440px;
}
</style>