<template>
    <div id="app">
      <h1>Log in as existing user</h1>
      <form v-on:submit.prevent="Login">
        <p>Username:<br></p>
        <input v-model="username" required name="username" id="username" placeholder='Username' @input="checkUsername">
        <br>
        <div id="warning" style="display:none; color:red">There is no such user. Please, enter another username</div>
        <br>
        <p>Password:<br></p>
        <input v-model="password" required type="password" name="password"
          id="password" placeholder='Secret password' @input="checkUsername">
        <br><br>
        <div id="passwd_err" style="display:none; color:red">Password for this user is incorrect. Please, try again</div>
        <br><br>
        <button id="btn" disabled>Log in!</button>
      </form>
    </div>
</template>

<script>
import {graphqlCommands} from '../graphql/graphql.js'
import { onLogin } from '../vue-apollo'

export default {
  name: 'Login',
  data()
  {
    return {
      username: '',
      password: '',
    }
  },
  methods:
  {
    async checkUsername()
    {
      const is_taken = await this.$apollo.query({
        query: graphqlCommands.CHECK_USERNAME,
        variables: {
          username: this.username,
        }
        });
      if(is_taken.data.user===false)
      {
        document.getElementById("btn").disabled=true;
        document.getElementById("warning").style.display="inline";
      }
      else
      {
        document.getElementById("warning").style.display="none";
        if(this.username.length!==0 && this.password.length!==0)
          document.getElementById("btn").disabled=false;
        else
          document.getElementById("btn").disabled=true;
      }
    },
    async Login()
    {
      document.getElementById("passwd_err").style.display="none";
      await this.$apollo.query({
        query: graphqlCommands.LOGIN,
        variables: {
          username: this.username,
          password: this.password,
        }
        })
        .then(async ({data}) => {
          await onLogin(this.$root.$apolloProvider.defaultClient, data.login);
          this.$router.push({
            name: 'Chatrooms'
          })
        })
        .catch((error) => {
          console.log(error);
          document.getElementById("passwd_err").style.display="inline";
        })
    }
  }
}
</script>

<style scoped>
h1 {
  margin: 40px 0 0;
}
a {
  color: #42b983;
}
</style>
