<template>
    <div id="app">
      <h1>Registering new user</h1>
      <form v-on:submit.prevent="Register">
        <p>Username:<br></p>
        <input v-model="username" required name="username" id="username" placeholder='Username' @input="checkUsername">
        <br>
        <div id="warning" style="display:none; color:red">This username is taken. Please, pick another username</div>
        <br>
        <p>Password:<br></p>
        <input v-model="password" required type="password" name="password"
          id="password" placeholder='Secret password' @input="checkUsername">
        <br><br>
        <button id="btn" disabled>Sign up!</button>
      </form>
    </div>
</template>

<script>
import { graphqlCommands } from '../graphql/graphql.js'

export default {
  name: 'Register',
  data()
  {
    return {
      user: [],
      username: '',
      password: '',
    }
  },
  methods:
  {
    async checkUsername()
    {
      const is_taken = await this.$apollo.query({
        query: graphqlCommands.CHECK_USERNAME,
        variables: {
          username: this.username,
        }
        });
      if(is_taken.data.user===true)
      {
        document.getElementById("btn").disabled=true;
        document.getElementById("warning").style.display="inline";
      }
      else
      {
        document.getElementById("warning").style.display="none";
        if(this.username.length!==0 && this.password.length!==0)
          document.getElementById("btn").disabled=false;
        else
          document.getElementById("btn").disabled=true;
      }
    },
    async Register()
    {
      await this.$apollo.query({
        query: graphqlCommands.REGISTER,
        variables: {
          username: this.username,
          password: this.password,
        }
      })
      .then(async () => {
        window.alert("Registration successful! Welcome to our secret club");
        this.$router.push({path: '/'});
      })
      .catch((error) => {
        console.log(error);
        window.alert("Oops,we're sorry, but something went wrong. Try again later");
      })
    }
  }
}
</script>

<style scoped>
h1 {
  margin: 40px 0 0;
}
a {
  color: #42b983;
}
</style>
