<template>
  <div id="app">
    <div id="nav">
      <h1>Welcome to Network Magica</h1><br>
      <div id="guest" style="display:inline;">
        <router-link to="/">Home</router-link>    ||    
        <router-link to="/login">Log in</router-link>    ||    
        <router-link to="/register">Sign up</router-link>
      </div>
      <div id="user" style="display:none;">
        <router-link to="/chatrooms">Chat rooms</router-link><br>
      </div>
    </div>
    <router-view/>
  </div>
</template>


<script>
import { getToken, onLogout } from './vue-apollo';

export default {
  name: 'App',
  mounted()
  {
    let apolloClient = this.$root.$apolloProvider.defaultClient;
    window.addEventListener('unload', async () => {
      await onLogout(apolloClient);
    })
  },
  watch:
  {
    $route()
    {
      const token = getToken();
      document.getElementById("user").style.display = token===null ? "none" : "inline";
      document.getElementById("guest").style.display = token===null ? "inline" : "none";
    }
  }
}
</script>


<style>
h1 {
  margin: 40px 0 0;
  color: #42b983;
}

#app {
  font-family: Avenir, Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
}

#nav {
  padding: 30px;
}

#nav a {
  font-weight: bold;
  color: #2c3e50;
}

